
"use client";

import * as React from "react";
import { Paperclip, SendHorizonal, Bot } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { ScrollArea } from "@/components/ui/scroll-area";
import { cn } from "@/lib/utils";
import { Icons } from "./icons";
import { getChatResponse } from "@/app/actions";
import { useToast } from "@/hooks/use-toast";

type Message = {
    role: 'user' | 'bot';
    text: string;
}

export function Chatbot() {
    const [isMounted, setIsMounted] = React.useState(false);
    const [isOpen, setIsOpen] = React.useState(false);
    const [messages, setMessages] = React.useState<Message[]>([]);
    const [input, setInput] = React.useState('');
    const [isLoading, setIsLoading] = React.useState(false);
    const { toast } = useToast();

    React.useEffect(() => {
        setIsMounted(true);
    }, []);

    const handleSendMessage = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!input.trim()) return;

        const userMessage: Message = { role: 'user', text: input };
        setMessages((prev) => [...prev, userMessage]);
        setInput('');
        setIsLoading(true);
        
        try {
            const history = messages.map(m => ({role: m.role, content: m.text}));
            const result = await getChatResponse(history, input);
            if (result.error) {
                 toast({ variant: 'destructive', title: "Chat Error", description: result.error });
                 setMessages(prev => prev.filter(m => m.text !== userMessage.text)); // remove user message on error
            } else if (result.response) {
                 const botMessage: Message = { role: 'bot', text: result.response };
                 setMessages((prev) => [...prev, botMessage]);
            }
        } catch (error) {
             toast({ variant: 'destructive', title: "Chat Error", description: "Could not get a response." });
             setMessages(prev => prev.filter(m => m.text !== userMessage.text)); // remove user message on error
        } finally {
            setIsLoading(false);
        }
    }

    if (!isMounted) {
        return null;
    }

    return (
        <div className="fixed bottom-4 right-4 z-50">
           {isOpen && (
             <div className="fixed inset-0 bg-background/80 backdrop-blur-sm sm:hidden" onClick={() => setIsOpen(false)}></div>
           )}
           {isOpen && (
             <Card className="fixed bottom-0 right-0 h-full w-full max-h-full sm:h-[600px] sm:w-96 sm:max-h-[80vh] sm:rounded-lg sm:bottom-20 sm:right-4 flex flex-col">
                <CardHeader className="flex flex-row items-center justify-between">
                    <div className="flex items-center gap-2">
                        <Bot className="h-6 w-6 text-primary" />
                        <div>
                            <CardTitle>BharatAI Assistant</CardTitle>
                            <CardDescription>How can I help you today?</CardDescription>
                        </div>
                    </div>
                </CardHeader>
                <CardContent className="flex-grow mb-4 overflow-hidden flex flex-col">
                   <ScrollArea className="flex-grow pr-4 -mr-4">
                        <div className="space-y-4 pr-4">
                            {messages.map((message, index) => (
                                <div key={index} className={cn("flex items-start gap-2", message.role === 'user' ? 'justify-end' : '')}>
                                    {message.role === 'bot' && <Icons.Logo className="h-6 w-6 text-primary flex-shrink-0" />}
                                    <div className={cn("rounded-lg px-3 py-2 text-sm max-w-[80%]", message.role === 'user' ? 'bg-primary text-primary-foreground' : 'bg-muted')}>
                                        {message.text}
                                    </div>
                                </div>
                            ))}
                             {isLoading && (
                                <div className="flex items-start gap-2">
                                     <Icons.Logo className="h-6 w-6 text-primary flex-shrink-0" />
                                     <div className="rounded-lg px-3 py-2 text-sm bg-muted">
                                        <Icons.Loader className="animate-spin" />
                                     </div>
                                </div>
                             )}
                        </div>
                   </ScrollArea>
                    <form onSubmit={handleSendMessage} className="flex w-full items-center space-x-2 pt-4">
                        <Input 
                            id="message" 
                            placeholder="Type your message..." 
                            className="flex-1" 
                            autoComplete="off" 
                            value={input}
                            onChange={(e) => setInput(e.target.value)}
                            disabled={isLoading}
                        />
                        <Button type="submit" size="icon" disabled={isLoading || !input.trim()}>
                            <SendHorizonal className="h-4 w-4" />
                            <span className="sr-only">Send</span>
                        </Button>
                    </form>
                </CardContent>
             </Card>
           )}
            <Button 
                size="icon" 
                className={cn("rounded-full h-14 w-14 transition-transform duration-300 ease-in-out", isOpen ? 'rotate-90 scale-0' : 'rotate-0 scale-100')} 
                onClick={() => setIsOpen(true)}
                aria-label="Open Chat"
                >
                <Icons.Logo className="h-8 w-8" />
            </Button>
            <Button 
                size="icon" 
                variant="destructive"
                className={cn("rounded-full h-14 w-14 absolute bottom-0 right-0 transition-transform duration-300 ease-in-out", !isOpen ? 'rotate-90 scale-0' : 'rotate-0 scale-100')} 
                onClick={() => setIsOpen(false)}
                 aria-label="Close Chat"
            >
                <Icons.X className="h-8 w-8" />
            </Button>
        </div>
    )
}
